import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { SigninDto } from 'src/app/shared/dtos/auth/signin-dto';
import { UserSigninService } from 'src/app/shared/services/signin.service';

@Component({
  selector: 'app-auth-signup',
  templateUrl: './auth-signup.component.html',
  styleUrls: ['./auth-signup.component.scss']
})
export class AuthSignupComponent extends AppComponentBase {
  user: SigninDto=new SigninDto();
  showPassword:boolean;
  showCPassword:boolean;
  saving=false;

  constructor(
      injector: Injector,
      private _userSigninService:UserSigninService
    ) { 
      super(injector)
    }

  ngOnInit() {
    this.showPassword=false;
    this.showCPassword=false;
  }

  signup(signForm:NgForm):void{
    if (this.user.password!==this.user.password_confirmation) {
      this.notify.error("Password not matched","Error");
      return;
    }
    this.saving=true;
    this._userSigninService.SigninUser("/api/register",this.user).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
        this.notify.success("User registered successfully","Success");
        signForm.resetForm();
    })
  }

}
